* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #222;
  color: burlywood;
  font-family: 'Montserrat', sans-serif;
  line-height: 1.8rem;
  max-width: 100vw;
  overflow-x: hidden;
}

a {
  display: block;
  margin: .5rem;
  padding: .25rem;
  color: orange;
  text-decoration: none;
}

a.active {
  background-color: rgba(200, 50, 0, .2);
}

.light-mode a {
  color: #3f72af;
}

.light-mode a.active {
  background-color: rgba(63, 114, 175, .2);
}

button {
  border: none;
  background-color: transparent;
  color: orange;
  cursor: pointer;
}

.light-mode button {
  color: #3f72af;
}

button:active,
button:focus {
  outline: none;
}

ul {
  list-style: none;
}

.light-mode * {
  transition: background-color .5s ease-in-out;
}